import Vue from 'vue';
import CodePreview from '@/components/CodePreview.vue';
import './ArticleLayout.less';

/**
 * ÊñáÁ´†Â∏ÉÂ±ÄÁöÑÈ´òÈò∂ÂáΩÊï∞ÁªÑ‰ª∂
 * @param components Ê≥®ÂÜåÁöÑÁªÑ‰ª∂ÂØπË±°
 * @param codeList Ê∫ê‰ª£Á†ÅÂàóË°®
 * @param chapterList Á´†ËäÇÁ¥¢ÂºïÔºåÁî®‰∫éÈÅçÂéÜÁ´†ËäÇÂíåÁ´†ËäÇÁöÑÊ∫ê‰ª£Á†Å
 */
const factoryArticleLayout = (
  components: Record<string, any>,
  codeList: Record<string, any>,
  chapterList: string[],
) => Vue.extend({
  data() {
    return {
      currentPage: '',
    };
  },

  components: {
    ...components,
    CodePreview,
  },

  render() {
    const {
      onclickPrev,
      onclickNext,
      currentIndex,
      isShow,
      createPromise,
    } = this;
    return (
      <article class="demo-article" ref="article">
        <header class="demo-article__header">
          <q-button-group>
            <q-button
              onclick={onclickPrev}
              disabled={currentIndex === 0}>
              ü§õ‰∏ä‰∏ÄÈ°µ
            </q-button>
            <q-button
              onclick={onclickNext}
              disabled={currentIndex === chapterList.length - 1}>
              ‰∏ã‰∏ÄÈ°µü§ú
            </q-button>
          </q-button-group>
        </header>
        <slot name="before"></slot>
        <section class="section-wrapper">
          {
            chapterList.map((chapter, index) => (
              isShow(chapter) ? (
                <div key={index}>
                  {/* chapter ÊòØÁ´†ËäÇÁªÑ‰ª∂ÔºåÊòØÈÄöËøá webpack ÁöÑ require.context Âä®ÊÄÅÂºïÂÖ•ÁöÑ */}
                  <chapter />
                  <code-preview codePromise={createPromise(chapter)} />
                </div>
              ) : null
            ))
          }
        </section>
      </article >
    );
  },

  mounted() {
    const { page } = this.$route.query;
    if (page) {
      this.currentPage = page.toString();
    }
    this.adjustCurrentPage();
  },

  computed: {
    currentIndex(): number {
      const { currentPage } = this;
      return chapterList.indexOf(currentPage);
    },
  },

  methods: {
    createPromise(name: string) {
      return codeList[name];
    },

    onclickPrev() {
      this.currentPage = chapterList[this.currentIndex - 1];
      this.adjustCurrentPage();
    },

    onclickNext() {
      this.currentPage = chapterList[this.currentIndex + 1];
      this.adjustCurrentPage();
    },

    adjustCurrentPage() {
      const { currentPage } = this;
      if (!chapterList.includes(currentPage)) {
        [this.currentPage] = chapterList;
      }
      this.changeRouter();
    },

    isShow(name: string): boolean {
      const { currentPage, $route } = this;
      return name === ($route.query.page || currentPage);
    },

    changeRouter() {
      console.count('changeRouter');
      try {
        // ËøôÈáåÂà§Êñ≠ fullPath ÊòØÂê¶Áõ∏Á≠âÔºåÈò≤Ê≠¢ÈáçÂ§çË∑≥ËΩ¨
        const { currentPage, $router, $route } = this;
        if (!$route.query.page || !currentPage || $route.query.page !== currentPage) {
          $router.push({
            name: this.$route.name as string,
            query: {
              page: currentPage,
            }
          });
        }
      } catch (error) {
        console.log(error);
      }
    },
  },
});

export default factoryArticleLayout;
